import ElementController from "Shared/ts/utils/element-controller";

export interface IAmazonCouponPacket {
    productCode: string;
}

interface CustomEventMap {
    "activate-checkbox": CustomEvent<IAmazonCouponPacket>;
}

declare global {
    interface HTMLElement {
        addEventListener<K extends keyof CustomEventMap>(
            type: K,
            listener: (this: HTMLElement, ev: CustomEventMap[K]) => void
        ): void;
    }
}

declare namespace _dtmShoppingCart {
    function Items(): any;

}

export default class AmazonCoupon extends HTMLElement {
    private _shadowRoot: ShadowRoot;

    private stylesheetUrl: string | null | undefined;

    private checkbox: HTMLInputElement | null;

    private label: HTMLLabelElement | null;

    private activateCheckboxEvent = new CustomEvent<IAmazonCouponPacket>(
        "activate-checkbox",
        {
            detail: <IAmazonCouponPacket>{}
        }
    );

    constructor(stylesheetUrl?: string) {
        super();

        const template = document.querySelector<HTMLTemplateElement>(
            "#amazon-coupon-template"
        );
        const content = template?.content ?? this.createHTMLTemplate();

        const shadowRoot = this.attachShadow({ mode: "closed" });
        shadowRoot.appendChild(content?.cloneNode(true));

        this._shadowRoot = shadowRoot;

        this.checkbox = shadowRoot.querySelector<HTMLInputElement>(
            'input[type="checkbox"]'
        );
        this.label = shadowRoot.querySelector<HTMLLabelElement>("label");

        this.stylesheetUrl =
            stylesheetUrl ??
            document
                .querySelector(`link[href*="style.css"]`)
                ?.getAttribute("href");
    }

    static get observedAttributes() {
        return ["product-code"];
    }

    private createHTMLTemplate(): DocumentFragment {
        const range = document.createRange();

        return range.createContextualFragment(`
        <div class="amazon-coupon">
            <div class="amazon-coupon__ribbon">
                <slot name="ribbon-text">Coupon!</slot>
            </div>
            <div class="amazon-coupon__checkbox-label">
                <div id="amazon-coupon-inactive" class="amazon-coupon__element">
                    <input
                        type="checkbox"
                        id="coupon"
                        name="coupon"
                        class="amazon-coupon__controller"
                        aria-labelledby="amazon-coupon-label"
                        aria-controls="amazon-coupon-active" />
                    <label for="coupon" class="amazon-coupon__label">
                        <span class="amazon-coupon__checkbox"></span>
                        <span id="amazon-coupon-label">
                            <slot name="inactive-text">Inactive text</slot>
                        </span>
                    </label>
                </div>
                <div id="amazon-coupon-active" class="amazon-coupon__element">
                    <svg class="amazon-coupon__checkmark" width="24" height="24" viewBox="0 0 24 24">
                        <path d="M20.285 2l-11.285 11.567-5.286-5.011-3.714 3.716 9 8.728 15-15.285z"/>
                    </svg>
                    <slot name="active-text">Active text</slot>
                </div>
            </div>
        </div>
        `);
    }

    private isProductInCart(productCode: string|null): boolean {
        if(productCode == null)
        {
            return false;
        }

        return _dtmShoppingCart.Items()[productCode];
    }

    private makeRequestUrl(url: string): URL {
        return /^\//.test(url)
            ? new URL(window.location.origin + url)
            : new URL(url);
    }

    private fallbackStylesheetProcess(stylesheetText: string): void {
        const refStyle = this._shadowRoot.querySelector("style");
        const style = refStyle ?? document.createElement("style");

        style.innerHTML = stylesheetText;

        refStyle
            ? this._shadowRoot.replaceChild(refStyle, style)
            : this._shadowRoot.appendChild(style);
    }

    private async initializeStylesheet(): Promise<void> {
        const url = this.getAttribute("stylesheet") ?? this.stylesheetUrl;
        if (!url) {
            throw {
                message:
                    "There is no stylesheet reference supported for the current <amazon-coupon> component",
                component: this
            };
        }

        const request = await fetch(this.makeRequestUrl(url));
        const response = await request.text();

        const canAdoptStyleSheets = "adoptedStyleSheets" in this._shadowRoot;

        if (!canAdoptStyleSheets) {
            return this.fallbackStylesheetProcess(response);
        }

        const stylesheet = new CSSStyleSheet();
        await stylesheet.replace(response);

        this._shadowRoot.adoptedStyleSheets = [stylesheet];
    }

    private initializeElementController(): void {
        const root = this._shadowRoot.querySelector(".amazon-coupon");
        if (!root) return;
        if (!this.checkbox) return;

        const elementController = new ElementController(root);

        Array.from(root.querySelectorAll(".amazon-coupon__controller")).forEach(
            (controller) => {
                controller.classList.add("element-controller__controller");
                elementController.controllers.push(controller);
            }
        );

        Array.from(root.querySelectorAll(".amazon-coupon__element")).forEach(
            (element) => {
                element.classList.add("element-controller__element");
                elementController.elements.push(element);
            }
        );

        elementController.controllers.forEach((controller) =>
            controller.addEventListener("click", (event) => {
                elementController.toggleElementsByController(controller);
            })
        );

        const inactiveElement = elementController.elements
            .filter((element): element is Element => !!element)
            .find((element) => element.id === "amazon-coupon-inactive");
        if (!inactiveElement) return;

        inactiveElement.setAttribute("data-element-controller-name", "");
    }

    private assignProductCodeToCheckbox(): void {
        const productCode = this.getAttribute("product-code");
        if (!productCode) {
            throw {
                message: `The product code attribute is missing for the current <amazon-coupon> component`,
                component: this
            };
        }

        if (!this.checkbox || !this.label) return;

        this.checkbox.id = productCode;
        this.label.setAttribute("for", productCode);

        

    }

    private initializeCheckboxEvent(): void {
        if (!this.checkbox) return;

        const id = this.checkbox.id;
        if (!id) return;

        this.checkbox.addEventListener("change", (event) => {
            this.activateCheckboxEvent.detail.productCode = id;
            this.dispatchEvent(this.activateCheckboxEvent);
        });
    }

    public connectedCallback(): void {
        this.initializeStylesheet().catch((error) => console.debug(error));

        try {
            this.assignProductCodeToCheckbox();
            this.initializeCheckboxEvent();
            this.initializeElementController();
            if(this.isProductInCart(this.getAttribute("product-code")))
            {
                if(this.checkbox != null)
                {
                    this.checkbox.click();
                }
            }
        } catch (e) {
            console.debug(e);
        }
    }

    public attributeChangedCallback(
        name: string,
        oldValue: string,
        newValue: string
    ): void {}
}

export const initializeAmazonCouponComponent = (): void => {
    window.customElements.define("amazon-coupon", AmazonCoupon);
};
